import React from "react";
import Typewriter from "typewriter-effect";

function Type() {
  return (
    <Typewriter
      options={{
        strings: [
          " 8 !",
          "Happy March 8th!",
          "Schnen 8. Mrz!",
          "Joyeux 8 mars !",
          "3  8 !",
          "Szczliwego 8 marca!"
        ],
        autoStart: true,
        loop: true,
        deleteSpeed: 50,
      }}
    />
  );
}

export default Type;
